input {
    border-radius: 2px;
    margin: 0 auto;
    background-color: #313131;
    display: inline-block;
    color: pink;
    font-family: "American Typewriter", serif;
    font-size: larger;
    text-align: center;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

.word {
    text-align: left;
    text-indent: 26px;
    letter-spacing: 56px;
    caret-color: transparent;
    -webkit-user-select: none;
    user-select: disabled;
    width: 344px;
    height: 49px;
}
.wc{
    width: 350px
}

.word.dm {
    border-color: black;
    background-color: #262626;
}

/******************* LETTER BOXES *******************/

.grey {
    background-color: grey;
    color: pink
}

.yellow {
    background-color: yellow;
    color: black
}

.green {
    background-color: lightgreen;
    color: black
}

.dm.grey {
    background-color: #110f0f;
    color: #610505
}

.dm.yellow {
    background-color: orange;
    color: white
}

.dm.green {
    background-color: darkgreen;
    color: #ffffff
}

/******************* KEYS *******************/

.key {
    border-color: inherit;
    width: 10%;
    color: pink;
    background-color: #313131;
}

.key2 {
    width: 11.11%;
    border-color: inherit;
}

.key3 {
    width: 10%;
    border-color: inherit;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}

#special-d {
    box-sizing: border-box;
    width: 15%;
    float: right;
    background-image: url(/better-wordle/static/media/backspace-icon.2d22e2c0.png);
    background-repeat: no-repeat;
    background-position: center;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
}

#special {
    box-sizing: border-box;
    width: 15%;
    padding-top: 4px;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

/******************* KEYBOARD BOX *******************/

.key-row {
    box-sizing: border-box;
    height: 33.33%;
    margin: 0 auto;
    border-color: inherit;
}

.key-box {
    height: 100%;
    width: 100%;
    max-width: 520px;
    border-color: thistle;
}

.key-box.dm {
    border-color: black;
}

/******************* BUTTONS *******************/

input.headerButton {
    border-radius: unset;
    margin: unset;
    background-color: unset;
    display: inline-block;
    color: black;
    text-transform: unset;
    border-radius: 2px;
    border-width: 2px;
    box-sizing: border-box;
    font-size: 130%;
    font-family: "Times New Roman", serif;
    width: 33.33%;
    height: 100%;
    min-width: 132px;
    display: inline-block;
    border-color: black;
    background-color: rgb(225, 225, 225);
}

input.headerButton:hover {
    background-color: rgb(203, 168, 202);
    -webkit-transform: scale3d(0.98, 0.96, 1);
            transform: scale3d(0.98, 0.96, 1);
}

input.headerButton.dm {
    background-color: #313131;
    color: #ffcfcf;
    border-color: #000000;
}

input.headerButton:hover.dm {
    background-color: #761919;
    color: #ff6982;
    -webkit-transform: scale3d(0.96, 0.96, 1);
            transform: scale3d(0.96, 0.96, 1);
}

/******************* ANSWER BOX *******************/

#ans-box {
    width: 100%;
    -webkit-user-select: none;
    user-select: disabled;
    font-size: 65%;
    background-color: #262626;
    color: pink;
    border-color: black;
    font-stretch: condensed;
    text-indent: -9999px;
    height: 36px;
}

/******************* ENDREGION *******************/
div {
    box-sizing: border-box;
}

div.row {
    box-sizing: border-box;
    display: inline-block;
    min-width: 200px;
    max-width:250px;
    width: 100%;
}

body {
    margin: 0 auto;
    padding-top: 30px;
    align-content: center;
    text-align: center;
    background-color: thistle;
}

#titleBox {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    box-sizing: border-box;
    min-width: 500px;
    width: 50%
}

.dm#titleBox {
    background-color: #712b06;
}

.big-box {
    margin: 0 auto;
    width: 100%;
}

.container {

    border: 3px solid rgb(255, 148, 71);
    margin: 0 auto;
    min-width: -moz-fit-content;
    min-width: -webkit-fit-content;
    min-width: fit-content;
}

.container.dm {
    border: 3px solid #712b06;
}

.wordContainer {
    width: 100%;
    height: .80px;
    min-height: 40.8px;
}

#wordBoxContainer{
    min-width: 1000px;
    margin: 0 auto;
    display: inline-block;   
}

#keyCont {
    min-width: 350px;
    width: 40%;
    max-width: 520px;
    height: 152px
}

p {
    border: 2px solid black;
    box-sizing: border-box;
    margin: 0;
    width: 100%;
    height: 100%;
    font-family: "American Typewriter", serif;
    font-size: 200%;
    display: inline-block;
}

.title-box {
    font-family: "Times New Roman", serif;
    font-size: 230%;
    display: block;
}

#plain {
    background-color: rgb(225, 225, 225);
}

#error {
    background-color: rgb(168, 14, 14);
}

.dm#plain {
    background-color: #262626;
    color: pink;
}

.dm#error {
    background-color: #570909;
    color: #000000
}

.letter {
    width: 20%;
    height: 100%;
    min-height: 36.8px;
    text-align: center;
}

#charColor-1 {
    background-color: rgb(225, 225, 225);
}

#charColor1 {
    background-color: rgb(99, 190, 93);
}

#charColor0 {
    background-color: rgb(255, 199, 30);
}

.dm#charColor-1 {
    background-color: #262626;
    color: pink;
}

.dm#charColor1 {
    background-color: #004500;
    color: white;
}

.dm#charColor0 {
    background-color: orange;
    color: #892d2d;
}
